<style>.skill-editor{display:inline-flex;margin-left:.5rem}input[type=text]{border:none;letter-spacing:-.01rem;margin:0 0 0 .2rem;width:100%}input[type=number]{border:none;border-bottom:.5pt solid #7f7f7f;margin:0;text-align:right;width:2.2rem}.center{position:relative;width:1rem}.center p{left:50%;margin:0;position:absolute;top:50%;transform:translate(-50%,-50%)}.selected,.selected input{background-color:#dfefff}mwc-icon{cursor:pointer;font-size:11pt;transition:box-shadow .1s}mwc-icon:hover{box-shadow:0 0 5px rgba(33,33,33,.8)}mwc-icon:active:hover{box-shadow:0 0 0 transparent}mwc-icon.disabled{color:#7f7f7f;cursor:auto;font-size:11pt;transition:box-shadow .1s}mwc-icon:hover.disabled{box-shadow:0 0 0 transparent}mwc-icon:active:hover.disabled{box-shadow:0 0 0 transparent}</style>

<script>
    import Skill from "../../../../model/Skill";
    import { createEventDispatcher } from 'svelte';

    const dispatch = createEventDispatcher();
    export let skill = new Skill();
    function handleChanged() {
        dispatch('skill_changed',skill);
    }

    function handleSelected() {
        dispatch('skill_selected',skill);
        selected=true;
    }

    function handleBlur() {
        selected=false;
    }

    function rollSkill() {
        dispatch('roll_skill',skill);
    }

    let selected=false;
</script>

<div class="skill-editor { selected ? 'selected' : ''}">
    <input type="text" bind:value={skill.name} on:focus={handleSelected} on:blur={handleBlur}/>
    <input type="number" min="0" max="999" bind:value="{skill.value}" on:change={handleChanged} on:focus={handleSelected} on:blur={handleBlur}/>
    <div class="center"><p style="display: inline-block">%</p></div>
    <input type="checkbox" bind:checked={skill.used} on:focus={handleSelected} on:blur={handleBlur}/>
    {#if (skill && skill.value && skill.name)}
        <mwc-icon class="noprint" on:click={rollSkill}>casino</mwc-icon>
    {:else}
        <mwc-icon class="noprint disabled" >casino</mwc-icon>
    {/if}
</div>