<style>input{width:1.8rem}input[type=number]{border:none;border-bottom:.5pt solid #7f7f7f;margin:0;text-align:right}input[type=text]{border:none;letter-spacing:-.01rem;margin:0}.name,.roll-name{display:inline-block;margin-left:.2rem}.name{width:2rem}.roll-name{padding-left:.5rem;width:4.3rem}.selected,.selected input{background-color:#dfefff}mwc-icon{cursor:pointer;font-size:11pt;transition:box-shadow .1s}mwc-icon:hover{box-shadow:0 0 5px rgba(33,33,33,.8)}mwc-icon:active:hover{box-shadow:0 0 0 transparent}mwc-icon.disabled{color:#7f7f7f;cursor:auto;font-size:11pt;transition:box-shadow .1s}mwc-icon:hover.disabled{box-shadow:0 0 0 transparent}mwc-icon:active:hover.disabled{box-shadow:0 0 0 transparent}</style>

<script>

    import Characteristic from "../../../../model/Characteristic";
    import { createEventDispatcher } from 'svelte';

    const dispatch = createEventDispatcher();
    export let characteristic = new Characteristic();

    function handleChanged() {
        characteristic.roll = characteristic.value*5;
        dispatch('characteristic_changed',characteristic.name);
    }
    function handleSelected() {
        dispatch('characteristic_selected',characteristic);
        selected=true;
    }

    function handleBlur() {
        selected=false;
    }

    function rollCharacteristic() {
        dispatch('roll_characteristic', characteristic);
    }

    let selected=false;
</script>

<div class="characteristic-editor { selected ? 'selected' : ''}">
    <label class="name" for="char__{characteristic.name}">{characteristic.name}</label>
    <input id="char__{characteristic.name}" type="number" min="1" max="20" bind:value="{characteristic.value}" on:change={handleChanged} on:focus={handleSelected} on:blur={handleBlur}/>
    {#if (characteristic.rollName)}
    <label class="roll-name" for="roll__{characteristic.name}">{characteristic.rollName}</label>
    <input id="roll__{characteristic.name}" type="number" min="1" max="999" bind:value="{characteristic.roll}" on:focus={handleSelected} on:blur={handleBlur}/>
    <mwc-icon class="noprint" on:click={rollCharacteristic}>casino</mwc-icon>
    {/if}
</div>